import { loginAPI, kakaoLogin } from "@api/axios";
import { RootState } from "@modules/index";
import { login } from "@modules/loginInfo"
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import LoginView from "./LoginView";

interface ILoginProps {
  onClose: () => void;
}

export interface ILoginForm {
  email: string;
  password: string;
}

function Login ({onClose}:ILoginProps) {
  const navigate = useNavigate()
  const loginInfo = useSelector((state:RootState) => state.loginInfo)
  const dispatch = useDispatch()
  const moveToJoin = () => {
    onClose()
    navigate('/join')
  }

  const onSubmit = (data:ILoginForm) => {
    //  API 
    loginAPI(data)
      .then(response => {
        // response   
        console.log(response)
      })
      .catch(error => {
        console.log(error)
      })
    //   (redux)
    // email, role, isLogined, name   
    alert(' ')
    dispatch(login(data))
    onClose()
    navigate('/')
  }
  
  const onKakaoLogin = () => {
    location.href = kakaoLogin
  }

  const props = {
    onSubmit,
    moveToJoin,
    onKakaoLogin
  }

  return (
    <LoginView {...props} />
  )
}

export default Login